@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*gradient for buttons: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);*/
/*gradient for text: (15deg, #1c78ad 0%, #72bbb2 100%)*/
* {
  margin: 0; }

#root {
  height: 100vh;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#c4c5c7), color-stop(52%, #dcdddf), to(#ebebeb));
  background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%);
  background-attachment: fixed;
  font-family: "Source Sans Pro", sans-serif; }

a {
  color: #598e89;
  text-decoration: none; }

h1,
h2,
h3,
h4 {
  font-weight: normal; }

h1 {
  text-align: center;
  padding: 10px; }

h5 {
  font-weight: normal;
  margin: 10px;
  text-align: center; }

.btn {
  background: -webkit-linear-gradient(15deg, #1c78ad 0%, #72bbb2 100%);
  margin: 10px;
  color: white; }

.apptH4 {
  color: #598e89; }

.apptLow {
  margin-bottom: 10px; }

.dlt {
  color: tomato; }

.appointmentCard,
.practiceCard {
  display: flex;
  justify-content: space-between; }

.appointments,
.practices {
  display: flex;
  align-items: center;
  padding: 10px;
  flex-direction: column; }

.landing {
  width: 100vw;
  height: 480px;
  background-image: url("https://images.unsplash.com/photo-1532938911079-1b06ac7ceec7?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1489&q=80");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  div-margin: 15px;
  div-text-align: left;
  div-display: flex;
  div-flex-direction: column;
  div-justify-content: flex-end; }

.title {
  background: -webkit-linear-gradient(15deg, #434343 0%, black 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 3.5rem;
  width: 330px;
  padding-left: 20px;
  text-align: left; }

.skew {
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  background-color: tomato;
  display: flex;
  justify-content: center; }

h3 {
  color: white; }

.button-container {
  display: flex;
  justify-content: space-around;
  padding: 20px; }

.home-btn {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#c4c5c7), color-stop(52%, #dcdddf), to(#ebebeb));
  background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%); }

